.container-previsao {
    width: 100%;

    h2 {
        font-family: var(--font);
        text-align: center;
        font-size: var(--title);
        color: var(--color-first);
        padding: 10px 0;
        text-shadow: 0.5px .5px 0.5px var(--color-second);
    }


    .box-result {
        width: 100%;
        display: flex;
        flex-direction: column;
        background-color: var(--color-second);
        color: var(--color-white);
        font-family: var(--font);
        gap: 7px;

        div:nth-child(1), div:nth-child(2), div:nth-child(3), div:nth-child(4) {
            width: 100%;
            display: flex;
            flex-direction: row;

            span:nth-child(1) {
                width: 450px;
                height: 50px;
                display: flex;
                align-items: center;
                font-family: var(--font);
                font-size: var(--paragraph);
                background: var(--color-second);
                color: var(--color-white);
                font-weight: 500;
                padding: 0px 10px;
                line-height: 25px;
            }

            span:nth-child(2) {
                width: 100%;
                height: 50px;
                background-color: var(--color-second);
                font-family: var(--font);
                font-size: var(--paragraph);
                color: var(--color-white);
                font-weight: 500;
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: right;
                padding: 0 10px 0 0;
                line-height: 25px;
           
            }

        }

        div:nth-child(5) {
            width: 100%;
            display: flex;
            flex-direction: column;

            span:nth-child(1) {
                width: 450px;
                height: 50px;
                display: flex;
                align-items: center;
                font-family: var(--font);
                font-size: var(--paragraph);
                background: var(--color-second);
                color: var(--color-white);
                font-weight: 500;
                padding: 0px 10px;
                line-height: 25px;
            }

            span:nth-child(2) {
                width: 100%;
                background-color: var(--color-second);
                font-family: var(--font);
                font-size: var(--paragraph);
                color: var(--color-white);
                font-weight: 500;
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: right;
                padding: 0 10px 0 0;
                line-height: 25px;
           
            }


        }


    }
}